
#loginForm{
    background-color: #F8BBD0;
    width: 260px;
    height: 180px;
    padding: 40px;
    margin: auto;
    margin-top: 88px; 
    border: 2px solid #FCE4EC;
    border-radius: 7px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);

}
#loginForm input{
    width: 250px;
    height: 30px;
    margin-bottom: 25px;
    border: none;
}
#loginForm button{
    width: 255px;
    height: 30px;
    background-color:#E91E63;
    color: white;
    border: none;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 4px 17px 0 rgba(0,0,0,0.19);
    
}
#loginForm button:hover {
    background-color: #E91E63;
  }
  
#loginForm button:active {
    background-color: #E91E63;
    box-shadow: 0 5px #f8bbd0;
    transform: translateY(4px);
  }
#loginForm .required::after{
    content:'*';
    color: red;
}