#calcu {
  background-color: #ff4081;
  width: 260px;
  height: 300px;
  padding: 35px;
  margin: auto;
  margin-top: 88px;
  border-radius: 10px;
  border: 2px solid #fce4ec;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);

}
#calcu .container {
  width: 300px;
  height: 50px;
}
#calcu button {
  background-color: #f8bbd0;
  width: 40px;
  height: 40px;
  margin-bottom: 100px;
  border-radius: 100%;
  border: none;
  margin-right: 35px;
  margin-top: 15px;
  font-size: 18px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 9px 15px 0 rgba(0, 0, 0, 0.19);
}
#calcu button:hover {
  background-color: #fce4ec;
}

#calcu button:active {
  background-color: #fff;
  box-shadow: 0 5px #f8bbd0;
  transform: translateY(4px);
}

#calcu input {
  width: 240px;
  height: 30px;
  text-align: end;
  padding: 15px;
  font-size: 30px;
}
#calcu .eq {
  width: 110px;
  border-radius: 100px;
}
