
#info .card {
  background-color: #f8bbd0;
  display: inline-block;
  width: 200px;
  height: 330px;
  padding: 15px;
  /* margin-top: 35px; */
  margin-left: 100px;
  border: 2px solid #FCE4EC;
  border-radius: 6px;
  /* box-shadow: 5px 5px 5px 5px #ec407a; */
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: absolute;
  /* top: 40; */
}
#info button {
  width: 50px;
  height: 25px;
  background-color: #e91e63;
  margin-left: 80px;
  margin-top: 2px;
  border-color:  #ec407a;
  color: black;
  font-weight: bolder;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
}

#info button:hover {
  background-color: #e91e63;
}

#info button:active {
  background-color: #e91e63;
  box-shadow: 0 5px #f8bbd0;
  transform: translateY(4px);
}

#info table {
  background-color: #f8bbd0;
  width: 600px;
  height: 335px;
  padding: 15px;
  margin-left: 350px;
  margin-top:60px;
  /* margin-bottom: 10px; */
  text-align: left;
  font-display: inherit;
  
}
#info td:hover {
  background-color: #ffd;
}
