
  #page .card {
    background-color: #F8BBD0;
    display: inline-block;
    width: 200px;
    height: 330px;
    padding: 15px;
    margin-right: 200px;
    margin-bottom: 25px;
    margin: 30px;
    /* margin-top: 150px; */
    margin-left: 60px;
    border: 2px solid #FCE4EC;
    border-radius: 10px;
    /* box-shadow: 5px 5px 5px 5px #EC407A; */
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    /* transition: 0.5s; */

    

  }
  #page button {
    width: 50px;
    height: 25px;
    background-color: #E91E63;
    margin-left: 80px;
    margin-top: 2px;
    border-color:#EC407A;
    color: black;
    font-weight: bolder;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 10px 0 rgba(0,0,0,0.19);

  }
  
  #page button:hover {
    background-color: #E91E63;
  }
  
  #page button:active {
    background-color: #E91E63;
    box-shadow: 0 5px #f8bbd0;
    transform: translateY(4px);
  }
  #page .card:hover {
    width: 200px;
    height: 330px;
   
  }

 
 